<template>
  <div class="roles_container">
    <!-- 面包屑导航 -->
    <bread :first="'权限管理'" :second="'角色列表'"></bread>
    <!-- 卡片导航 -->
    <el-card class="box-card">
      <el-button type="primary" @click="dialogFormVisibleAdd = true">添加角色</el-button>
      <!-- 表格 -->
      <template class="table">
        <el-table :data="tableData" style="width: 100%" border>
          <!-- 展开项 -->
          <el-table-column type="expand" label="展开项" width="70">
            <!-- 栅格布局 -->
            <template slot-scope="scope">
              <!-- 最外层 -->
              <el-row v-for="(item, index) in scope.row.children" :key="index" class="firstRows">
                <!-- 第一层左边 -->
                <el-col :span="4">
                  <el-tag effect="dark" type="primary" closable @close="deleteTag(item, scope.row)">
                    {{ item.authName }}
                  </el-tag>
                  <i class="el-icon-caret-right" style="margin-left:10px"></i>
                </el-col>

                <!-- 第一层右边 -->
                <el-col :span="20">
                  <!-- 第二层循环 -->
                  <el-row v-for="(item2, index2) in item.children" :key="index2" class="secondRows">
                    <!-- 第二层左边 -->
                    <el-col :span="6">
                      <el-tag effect="dark" type="success" closable @close="deleteTag(item2, scope.row)">
                        {{ item2.authName }}
                      </el-tag>
                      <i class="el-icon-caret-right" style="margin-left:10px"></i>
                    </el-col>
                    <!-- 第二层右边 -->
                    <el-col :span="18" class="sanji">
                      <el-tag v-for="(item3, index3) in item2.children" :key="index3" effect="dark" type="warning" class="sanjiBtn" closable @close="deleteTag(item3, scope.row)">
                        {{ item3.authName }}===={{ item3.id }}
                      </el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <!-- 序号 -->
          <el-table-column label="#" width="50">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.$index + 1 }}</span>
            </template>
          </el-table-column>
          <el-table-column label="角色姓名" prop="roleName"> </el-table-column>
          <el-table-column label="角色描述" prop="roleDesc"> </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="editBtn(scope.row)">编辑</el-button>
              <el-button type="warning" size="mini" icon="el-icon-delete" @click="deleteRole(scope.row)">删除</el-button>
              <el-button type="danger" size="mini" icon="el-icon-setting" @click="editLeavel(scope.row)">分配权限</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </el-card>

    <!-- 分配权限弹出层 -->
    <el-dialog title="分配权限" :visible.sync="dialogFormVisible">
      <el-tree :data="treeData" show-checkbox node-key="id" ref="tree" default-expand-all :default-expanded-keys="[2, 3]" :default-checked-keys="defaultKeys" :props="defaultProps"> </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRoles">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 添加角色的弹出层 -->
    <el-dialog title="添加角色" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="addForm" :label-width="formLabelWidth" :rules="rules" ref="addForm">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model.trim="addForm.roleName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" :label-width="formLabelWidth" prop="roleDesc">
          <el-input v-model.trim="addForm.roleDesc" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改角色的弹出框 -->
    <el-dialog title="修改角色" :visible.sync="dialogFormVisibleEdit">
      <el-form :model="editForm" :label-width="formLabelWidth" :rules="rules" ref="editForm">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model.trim="editForm.roleName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" :label-width="formLabelWidth" prop="roleDesc">
          <el-input v-model.trim="editForm.roleDesc" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleEdit = false">取 消</el-button>
        <el-button type="primary" @click="editRole">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tableData: [], // 表格的数据
      treeData: [], // 表格展开的权限的树状图
      defaultKeys: [], // 默认选中的树状列表
      rolesId: '', // 用来存放点击的那个id
      defaultProps: {
        children: 'children',
        label: 'authName' // 树状图展开展示的属性
      },
      dialogFormVisible: false,

      // 添加角色的对话框
      dialogFormVisibleAdd: false,
      formLabelWidth: '80px',
      addForm: {
        roleName: '',
        roleDesc: ''
      }, // 添加角色表单参数
      rules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          { min: 2, max: 39, message: '长度在 2 到 39 个字符', trigger: 'blur' }
        ]
      },
      // 修改角色
      editForm: {
        roleName: '',
        roleDesc: ''
      },
      dialogFormVisibleEdit: false
    }
  },
  created () {
    this.getRoles()
  },
  methods: {
    // 获取数据
    async getRoles () {
      const res = await this.axios('/roles')
      this.tableData = res.data.data
      // console.log(res.data.data, 88888)
    },
    // 点击分配权限按钮
    async editLeavel (rows) {
      // 先清空上一次的权限数据
      this.defaultKeys = []
      // 将用户的id存入 便于添加权限时上传
      this.rolesId = rows.id
      // 发送请求获取数据
      const res = await this.axios('/rights/tree ')
      // console.log(res, 8888)
      // 将获取的数据放在数组数组中
      this.treeData = res.data.data
      // 显示对话框
      this.dialogFormVisible = true
      // 递归获取权限
      this.recursion(rows, this.defaultKeys)
    },
    // 递归获取最里层的id
    recursion (rows, arr) {
      // 判断是否自己还存在children
      if (!rows.children) {
        // 不存在
        arr.push(rows.id)
        return false
      }
      // 存在children
      // 遍历这个children
      // 将这个对象的每个属性都进行递归，判断子属性还有没有children
      rows.children.forEach(ele => {
        // 使用递归
        this.recursion(ele, arr)
      })
    },
    // 确认修改权限
    async editRoles () {
      // console.log(this.$refs['tree'].getCheckedKeys(), 999999) //获取所有的子项全选的子项id和父id
      // console.log(this.$refs['tree'].getHalfCheckedKeys(), 888888)
      const arr = [...this.$refs.tree.getCheckedKeys(), ...this.$refs.tree.getHalfCheckedKeys()]
      const rids = arr.join(',')
      // 发送请求
      const res = await this.axios.post(`/roles/${this.rolesId}/rights`, {
        rids
      })
      if (res.data.meta.status === 200) {
        this.$message({
          message: res.data.meta.msg,
          type: 'success'
        })
      }
      // console.log(res, 88888)
      this.dialogFormVisible = false // 关闭弹窗
      // 刷新页面
      this.getRoles()
    },
    // 提交添加用户信息
    addRole () {
      this.$refs.addForm.validate(async item => {
        if (item) {
          // 发送请求
          const res = await this.axios.post('/roles', this.addForm)
          if (res.data.meta.status === 201) {
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.dialogFormVisibleAdd = false
            this.getRoles()
          }
        }
      })
    },
    // 打开修改用户按钮
    editBtn (rows) {
      // 利用双向数据绑定显示数据
      this.editForm.roleName = rows.roleName
      this.editForm.roleDesc = rows.roleDesc
      this.editForm.id = rows.id
      this.dialogFormVisibleEdit = true
    },
    // 编辑提交角色
    editRole () {
      // 提交数据
      this.$refs.editForm.validate(async item => {
        if (item) {
          // 发送请求
          const res = await this.axios.put(`/roles/${this.editForm.id}`, this.editForm)
          if (res.data.meta.status === 200) {
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.dialogFormVisibleEdit = false
            this.getRoles()
          }
        }
      })
    },
    // 删除角色
    deleteRole ({ id }) {
      this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.axios.delete(`/roles/${id}`)
          if (res.data.meta.status === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            // 刷新
            this.getRoles()
          }
        })
        .catch(() => {})
    },
    // 删除指定权限
    deleteTag (w, rows) {
      this.$confirm('是否删除该权限？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.axios.delete(`/roles/${rows.id}/rights/${w.id}`)
          if (res.data.meta.status === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            // 刷新

            // this.getRoles() //不建议 会全局刷新导致表格重新渲染合上
            rows.children = res.data.data // 刷新当前用户所有权限的数组
          }
        })
        .catch()
    }
  }
}
</script>
<style lang="less" scoped>
.roles_container {
  // 卡片
  .box-card {
    width: 100%;
    height: 100%;
    margin-top: 20px;
    // 表格
    .el-table {
      margin: 20px 0;
    }
  }

  .firstRows {
    &:nth-child(1) {
      border-top: 1px solid #efefef;
    }
    border: 1px solid #efefef;
    border-top: 0;
    display: flex;
    align-items: center;
    text-align: center;
    .secondRows {
      padding: 10px 0;
      display: flex;
      align-items: center;
      border: 1px solid #efefef;
      border-bottom: 0;
      &:nth-child(1) {
        border-top: 0;
      }
      .el-col-19 {
        text-align: left;
      }
    }
  }
  .sanji {
    text-align: left;
  }
  .sanjiBtn {
    margin: 5px;
  }
}
</style>
