<template>
  <div class="user_container">
    <!-- 面包屑导航 -->
    <bread :first="'用户管理'" :second="'用户列表'"></bread>
    <!-- 卡片导航 -->
    <el-card class="box-card">
      <!-- 搜索框 -->
      <div class="search">
        <el-input placeholder="请输入内容" v-model="params.query" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" @click="getData"></el-button>
        </el-input>
        <!-- 按钮 -->
        <el-button type="primary" class="addBtn" @click="dialogFormVisible = true">添加用户</el-button>
      </div>
      <!-- 表格 -->
      <template class="table">
        <el-table :data="tableData" style="width: 100%" border>
          <!-- 序号 -->
          <el-table-column label="#" width="50">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.$index + 1 }}</span>
            </template>
          </el-table-column>
          <el-table-column label="姓名" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.username }}</span>
            </template>
          </el-table-column>
          <el-table-column label="邮箱" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.email }}</span>
            </template>
          </el-table-column>
          <el-table-column label="电话" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.mobile }}</span>
            </template>
          </el-table-column>
          <el-table-column label="角色" width="180">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.role_name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="状态" width="180">
            <template slot-scope="scope">
              <el-switch v-model="scope.row.mg_state" active-color="#409EFF" inactive-color="#DCDFE6" @change="swichChange(scope.row)"> </el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="editUser(scope.row)"></el-button>
              <el-button type="warning" size="mini" icon="el-icon-delete" @click="delUser(scope.row)"></el-button>
              <el-button type="danger" size="mini" icon="el-icon-setting" @click="allot(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!-- 分页功能 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[2, 5, 10, 50]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 添加对话框 -->
    <el-dialog title="添加用户" :visible.sync="dialogFormVisible">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
        <el-form-item label="用户名" :label-width="formLabelWidth" prop="username">
          <el-input v-model="ruleForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth" prop="password">
          <el-input v-model="ruleForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
          <el-input v-model="ruleForm.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="电话" :label-width="formLabelWidth" prop="mobile">
          <el-input v-model="ruleForm.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeAdd">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改对话框 -->
    <el-dialog title="编辑用户：" :visible.sync="dialogFormVisibleEdit">
      <el-form :model="editRuleForm" :rules="rules" ref="editRuleForm">
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input v-model="editRuleForm.username" autocomplete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
          <el-input v-model="editRuleForm.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="电话" :label-width="formLabelWidth" prop="mobile">
          <el-input v-model="editRuleForm.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleEdit = false">取 消</el-button>
        <el-button type="primary" @click="editUserBtn">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 分配角色 -->
    <el-dialog title="分配角色：" :visible.sync="dialogFormVisibleAllot">
      <el-form :model="userinfo">
        <el-form-item label="当前的用户：" label-width="100px">{{ userinfo.username }}</el-form-item>
        <el-form-item label="当前的角色：" label-width="100px">
          {{ userinfo.role_name }}
        </el-form-item>
        <el-form-item label="分配新角色：" label-width="100px">
          <el-select v-model="rid" placeholder="请选择">
            <el-option :label="item.roleName" :value="item.id" v-for="(item, index) in roleTable" :key="index"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAllot = false">取 消</el-button>
        <el-button type="primary" @click="changeAllot">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    // 密码验证
    var validatePass = (rule, value, callback) => {
      var reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/
      if (!reg.test(value)) {
        callback(new Error('密码至少包含 数字和英文，长度6-20'))
      } else {
        callback()
      }
    }
    // 邮箱验证
    var validateEmail = (rule, value, callback) => {
      var reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/
      if (!reg.test(value)) {
        callback(new Error('邮箱不合规'))
      } else {
        callback()
      }
    }
    // 电话验证
    var validatePhone = (rule, value, callback) => {
      var reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57]|19[0-9])[0-9]{8}$/
      if (!reg.test(value)) {
        callback(new Error('请输入正确的手机号!'))
      } else {
        callback()
      }
    }
    return {
      tableData: [],
      total: 0,
      params: {
        query: '', // 查询参数 非必要参数
        pagenum: 1, // 当前页码数
        pagesize: 2 // 每页显示条数
      },
      currentPage4: 1,
      // 添加对话框
      dialogFormVisible: false,
      // 修改对话框
      dialogFormVisibleEdit: false,
      formLabelWidth: '80px',
      // 添加表单参数
      ruleForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 编辑表单数据
      editRuleForm: {
        id: '',
        username: '',
        email: '',
        mobile: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: validateEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入电话', trigger: 'blur' },
          { validator: validatePhone, trigger: 'blur' }
        ]
      },
      // 分配角色对话框
      dialogFormVisibleAllot: false,
      roleTable: [], // 角色数据,
      // 当前用户的信息
      userinfo: {},
      rid: '' // 角色ID
    }
  },
  created () {
    this.getData()
  },
  methods: {
    // 获取数据的函数
    async getData () {
      const res = await this.axios.get('/users', { params: this.params })
      // 将获取到的数据存到本地参数
      this.total = res.data.data.total
      // console.log(res.data.data.total,88888);
      this.tableData = res.data.data.users
    },
    // 修改每页的显示条数
    handleSizeChange (val) {
      // 将用户键入的条数赋值给data的数据
      console.log(val)
      this.params.pagesize = val
      // 重新调用获取数据的函数
      this.getData()
      // console.log(`每页 ${val} 条`)
    },
    // 修改当前显示页
    handleCurrentChange (val) {
      this.params.pagenum = val
      this.getData()
      // console.log(`当前页: ${val}`)
    },
    // 添加用户
    addUser () {
      this.$refs.ruleForm.validate(async valid => {
        if (valid) {
          const res = await this.axios.post('/users', this.ruleForm)
          if (res.data.meta.status === 201) {
            // 创建成功
            // 弹出提示
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.$refs.ruleForm.resetFields() // 重置表单
            // 关闭对话框
            this.dialogFormVisible = false
            // 刷新页面
            this.getData()
          } else {
            this.$message({
              showClose: true,
              message: res.data.meta.msg,
              type: 'error'
            })
          }
          console.log(res.data.meta.status)
        } else {
          return false
        }
      })
    },
    // 取消添加
    closeAdd () {
      this.dialogFormVisible = false
      this.$refs.ruleForm.resetFields()
    }, // 状态开关变化
    async swichChange (rows) {
      //  发送请求修改状态
      const res = await this.axios.put(`/users/${rows.id}/state/${rows.mg_state}`)
      if (res.data.meta.status === 200) {
        // 弹出提示
        this.$message({
          message: res.data.meta.msg,
          type: 'success'
        })
      } else {
        this.$message({
          showClose: true,
          message: res.data.meta.msg,
          type: 'error'
        })
      }
    },
    // 删除
    delUser ({ id }) {
      this.$confirm('是否删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.axios.delete(`/users/${id}`)
          if (res.data.meta.status === 200) {
            // 弹出提示
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.getData()
          } else {
            this.$message({
              showClose: true,
              message: res.data.meta.msg,
              type: 'error'
            })
          }
        })
        .catch(() => {})
    },
    // 打开修改表单修改
    async editUser ({ id }) {
      this.dialogFormVisibleEdit = true // 打开表单
      const res = await this.axios(`users/${id}`)
      this.editRuleForm.username = res.data.data.username
      this.editRuleForm.email = res.data.data.email
      this.editRuleForm.mobile = res.data.data.mobile
      this.editRuleForm.id = res.data.data.id
    },
    // 提交修改后的数据
    editUserBtn () {
      // 二次验证
      this.$refs.editRuleForm.validate(async valid => {
        if (valid) {
          // 使用es7 语法处理请求
          // axios 传递参数要求对象，此处 data里面的 ruleForm 本就是对象 故直接使用
          const res = await this.axios.put(`/users/${this.editRuleForm.id}`, this.editRuleForm)
          if (res.data.meta.status === 200) {
            // 弹出提示
            this.$message({
              message: res.data.meta.msg,
              type: 'success'
            })
            this.getData()
            this.dialogFormVisibleEdit = false
          } else {
            this.$message({
              showClose: true,
              message: res.data.meta.msg,
              type: 'error'
            })
          }
        } else {
          return false
        }
      })
    },
    // 分配权限
    async allot (msg) {
      this.userinfo = {} // 清空之前的数据
      // 发送请求获取角色列表
      const res = await this.axios('/roles')
      this.roleTable = res.data.data
      // console.log(msg, 99999)
      this.userinfo = msg
      this.dialogFormVisibleAllot = true
    },
    // 确定修改权限
    async changeAllot () {
      const res = await this.axios.put(`/users/${this.userinfo.id}/role`, {
        rid: this.rid
      })
      if (res.data.meta.status === 200) {
        this.$message({
          message: res.data.meta.msg,
          type: 'success'
        })
      }
      // console.log(res, 88888)
      this.getData()
      this.dialogFormVisibleAllot = false
    }
  }
}
</script>
<style lang="less" scoped>
.user_container {
  // 卡片栏
  .box-card {
    margin-top: 10px;
    // 搜索框
    .search {
      display: flex;
      .el-input-group {
        width: 40%;
        margin-right: 20px;
      }
    }
    .el-table {
      margin: 20px 0;
    }
  }
}
</style>
